.contactMain {
  background-color: #0e0d10;
  display: flex;
  flex-direction: row;
  padding-left: calc(20% - 28px);
  padding-right: 20px;
  gap: 28px;
}
.contact {
  flex-grow: 1;
  margin-top: 26px;
}
.Socials{
  display: flex;
  flex-direction: row;
  gap: 28px 0;
  flex-wrap: wrap;
  margin-top: 26px;
  margin-bottom: 78px;
}
.contactHead{
  font-size: 26px;
  margin-top: 74px;
  font-weight: 400;
  color: white;
}
.SocialImg {
  width: 26px;
  height: 26px;
  margin-right: 10px;
}
.SocialText {
  font-size: 18px;
  font-weight: 300;
}
.SocialLink{
  min-width: 33%;
  text-decoration: none;
  color: #5918df;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}
.MadeBy{
  margin-bottom: 12px;

}
.MadeByText{
  color: rgb(137, 137, 137);
}

@media (max-width: 1000px) {
  .Socials {
    flex-direction: column;
  }
  .SocialLink{
    width: 100%;
  }
}
@media (max-width: 450px){
  .contactHead {
    font-size: 24px;
  }
  .SocialText {
    font-size: 16px;
  }
  .SocialImg {
    width: 22px;
    height: 22px;
  }
  .contactMain {
  }
  .Socials {
    margin-bottom: 40px;
  }
  .MadeBy{
  }
  .MadeByText{
    font-size: 14px;
  }
}